// The exported code uses Tailwind CSS. Install Tailwind CSS in your dev environment to ensure all styles work.
"use client";

import React, { useState, useEffect } from 'react';
import styles from './login.module.css'; 

export function LoginForm() {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [showPassword, setShowPassword] = useState<boolean>(false);
  const [username, setUsername] = useState<string>('');
  const [password, setPassword] = useState<string>('');
  const [rememberMe, setRememberMe] = useState<boolean>(false);
  const [usernameFocused, setUsernameFocused] = useState<boolean>(false);
  const [passwordFocused, setPasswordFocused] = useState<boolean>(false);
  const [usernameValid, setUsernameValid] = useState<boolean | null>(null);
  const [passwordValid, setPasswordValid] = useState<boolean | null>(null);
  const [loginStatus, setLoginStatus] = useState<'idle' | 'success' | 'error'>('idle');

  // Validate username
  useEffect(() => {
    if (username.length > 0) {
      setUsernameValid(username.length >= 3); // Example: Minimum 3 characters
    } else {
      setUsernameValid(null);
    }
  }, [username]);

  // Validate password
  useEffect(() => {
    if (password.length > 0) {
      setPasswordValid(password.length >= 6);
    } else {
      setPasswordValid(null);
    }
  }, [password]);

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault();
    if (usernameValid && passwordValid) {
      setIsLoading(true);
      
      // Simulate API call
      setTimeout(() => {
        setIsLoading(false);
        // For demo purposes, always succeed
        setLoginStatus('success');
        
        // Reset after showing success message
        setTimeout(() => {
          setLoginStatus('idle');
        }, 3000);
      }, 2000);
    } else {
      setLoginStatus('error');
      setTimeout(() => {
        setLoginStatus('idle');
      }, 3000);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-100 to-purple-100 relative overflow-hidden">
      {/* Animated background particles */}
      <div className="absolute inset-0 overflow-hidden">
        {[...Array(20)].map((_, i) => (
          <div 
            key={i}
            className="absolute rounded-full bg-white opacity-20"
            style={{
              width: `${Math.random() * 20 + 10}px`,
              height: `${Math.random() * 20 + 10}px`,
              top: `${Math.random() * 100}%`,
              left: `${Math.random() * 100}%`,
              animation: `${styles.float} ${Math.random() * 10 + 10}s linear infinite`,
              animationDelay: `${Math.random() * 5}s`,
            }}
          />
        ))}
      </div>
      
      {/* Login container */}
      <div className="w-full max-w-md p-8 mx-4 bg-white bg-opacity-20 backdrop-blur-lg rounded-xl shadow-xl transform transition-all duration-500 hover:shadow-2xl animate-fade-in-up">
        {/* Header */}
        <div className="text-center mb-8 animate-fade-in-down">
          <div className="inline-block mb-4">
            <i className="fas fa-cube text-4xl text-blue-600 animate-bounce-slow"></i>
          </div>
          <h1 className="text-3xl font-bold text-gray-800 mb-2">Welcome Back</h1>
          <p className="text-gray-600 animate-typewriter overflow-hidden whitespace-nowrap">Sign in to access your account</p>
        </div>
        
        {/* Login form */}
        <form onSubmit={handleLogin} className="space-y-6">
          {/* Username field */}
          <div className="relative">
            <input
              type="text"
              id="username"
              className={`w-full px-4 py-3 bg-white bg-opacity-70 rounded-lg border-none outline-none transition-all duration-300 text-sm ${
                usernameFocused || username ? 'pt-6 pb-2' : ''
              } ${
                usernameValid === false ? 'ring-2 ring-red-400' : 
                usernameValid === true ? 'ring-2 ring-green-400' : ''
              }`}
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              onFocus={() => setUsernameFocused(true)}
              onBlur={() => setUsernameFocused(false)}
            />
            <label 
              htmlFor="username" 
              className={`absolute left-4 transition-all duration-300 pointer-events-none text-gray-500 ${
                usernameFocused || username ? 'text-xs top-1' : 'text-sm top-3'
              }`}
            >
              Username
            </label>
            {usernameValid === false && (
              <div className="absolute right-3 top-3 text-red-500 animate-fade-in">
                <i className="fas fa-exclamation-circle"></i>
              </div>
            )}
            {usernameValid === true && (
              <div className="absolute right-3 top-3 text-green-500 animate-fade-in">
                <i className="fas fa-check-circle"></i>
              </div>
            )}
          </div>
          
          {/* Password field */}
          <div className="relative">
            <input
              type={showPassword ? "text" : "password"}
              id="password"
              className={`w-full px-4 py-3 bg-white bg-opacity-70 rounded-lg border-none outline-none transition-all duration-300 text-sm ${
                passwordFocused || password ? 'pt-6 pb-2' : ''
              } ${
                passwordValid === false ? 'ring-2 ring-red-400' : 
                passwordValid === true ? 'ring-2 ring-green-400' : ''
              }`}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              onFocus={() => setPasswordFocused(true)}
              onBlur={() => setPasswordFocused(false)}
            />
            <label 
              htmlFor="password" 
              className={`absolute left-4 transition-all duration-300 pointer-events-none text-gray-500 ${
                passwordFocused || password ? 'text-xs top-1' : 'text-sm top-3'
              }`}
            >
              Password
            </label>
            <button 
              type="button"
              className="absolute right-3 top-3 text-gray-500 hover:text-gray-700 transition-colors cursor-pointer"
              onClick={() => setShowPassword(!showPassword)}
            >
              <i className={`fas ${showPassword ? 'fa-eye-slash' : 'fa-eye'}`}></i>
            </button>
          </div>
          
          {/* Remember me & Forgot password */}
          <div className="flex items-center justify-between">
            <div className="flex items-center">
              <div 
                className={`w-5 h-5 mr-2 flex items-center justify-center rounded border transition-all duration-300 cursor-pointer ${
                  rememberMe ? 'bg-blue-600 border-blue-600' : 'bg-white bg-opacity-70 border-gray-300'
                }`}
                onClick={() => setRememberMe(!rememberMe)}
              >
                {rememberMe && <i className="fas fa-check text-white text-xs"></i>}
              </div>
              <label htmlFor="remember" className="text-sm text-gray-600 cursor-pointer" onClick={() => setRememberMe(!rememberMe)}>
                Remember me
              </label>
            </div>
            <a href="#" className="text-sm text-blue-600 hover:text-blue-800 transition-colors hover:underline cursor-pointer whitespace-nowrap">
              Forgot password?
            </a>
          </div>
          
          {/* Login button */}
          <button
            type="submit"
            disabled={isLoading}
            className={`w-full py-3 px-4 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-lg font-medium transition-all duration-300 transform hover:translate-y-[-2px] hover:shadow-lg !rounded-button whitespace-nowrap cursor-pointer ${
              isLoading ? 'opacity-90' : ''
            }`}
          >
            {isLoading ? (
              <span className="flex items-center justify-center">
                <i className="fas fa-circle-notch fa-spin mr-2"></i>
                Signing in...
              </span>
            ) : (
              'Sign In'
            )}
          </button>
          
          {/* Status messages */}
          {loginStatus === 'success' && (
            <div className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded relative animate-fade-in" role="alert">
              <span className="block sm:inline">Login successful! Redirecting...</span>
            </div>
          )}
          
          {loginStatus === 'error' && (
            <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative animate-fade-in" role="alert">
              <span className="block sm:inline">Invalid username or password. Please try again.</span>
            </div>
          )}
          
          {/* Social login */}
          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300"></div>
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white bg-opacity-70 text-gray-500">Or continue with</span>
              </div>
            </div>
            
            <div className="mt-6 grid grid-cols-3 gap-3">
              <button
                type="button"
                className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white bg-opacity-70 text-sm font-medium text-gray-500 hover:bg-gray-50 transition-transform hover:scale-105 !rounded-button whitespace-nowrap cursor-pointer"
              >
                <i className="fab fa-google text-red-500"></i>
              </button>
              <button
                type="button"
                className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white bg-opacity-70 text-sm font-medium text-gray-500 hover:bg-gray-50 transition-transform hover:scale-105 !rounded-button whitespace-nowrap cursor-pointer"
              >
                <i className="fab fa-facebook text-blue-600"></i>
              </button>
              <button
                type="button"
                className="w-full inline-flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm bg-white bg-opacity-70 text-sm font-medium text-gray-500 hover:bg-gray-50 transition-transform hover:scale-105 !rounded-button whitespace-nowrap cursor-pointer"
              >
                <i className="fab fa-apple text-gray-800"></i>
              </button>
            </div>
          </div>
        </form>
        
        {/* Sign up link */}
        <p className="mt-8 text-center text-sm text-gray-600">
          Don't have an account?{' '}
          <a href="#" className="font-medium text-blue-600 hover:text-blue-500 transition-colors hover:underline cursor-pointer">
            Sign up now
          </a>
        </p>
      </div>
    </div>
  );
};
